.btn {
    display: flex;
    width: 153px;
    height: 50px;
    padding: 15px 58px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 30px;
    text-transform: uppercase;
    z-index: 5;
    cursor: pointer;
}

